"use client";

import { useState, useEffect } from "react";
import MapWithRouting from "./MapWithRouting";
import { getSheetData } from "../lib/fetchGoogleSheet";

export default function StoreLocator() {
  const [locations, setLocations] = useState([]);
  const [activeLocation, setActiveLocation] = useState(null);

  useEffect(() => {
    async function load() {
      try {
        const rows = await getSheetData("Locations") ?? []; // prevent undefined
        const formatted = rows.map((row: any) => ({
          name: row.Name,
          lat: Number(row.Lat),
          lng: Number(row.Lng),
          address: row.Address,
        }));

        setLocations(formatted);
        setActiveLocation(formatted[0] || null);
      } catch (e) {
        console.error("Error loading sheet:", e);
      }
    }
    load();
  }, []);

  // ðŸ‘‰ FIX: Safely handle undefined/null
  if (!locations || locations.length === 0) {
    return <p className="text-center text-slate-500 py-10">Loading locations...</p>;
  }

  return (
    <section className="max-w-6xl mx-auto p-6">
      <h2 className="text-3xl font-bold mb-6 text-center">Find Our Offices</h2>

      {/* Pills */}
      <div className="flex gap-4 overflow-x-auto pb-3 mb-4">
        {locations.map((loc, idx) => (
          <button
            key={idx}
            onClick={() => setActiveLocation(loc)}
            className={`px-5 py-2 rounded-full transition shrink-0 border
              ${activeLocation?.name === loc.name 
              ? "bg-slate-900 text-white" 
              : "bg-white text-slate-600 border-slate-300"}`}
          >
            {loc.name}
          </button>
        ))}
      </div>

      {/* Map */}
      {activeLocation && <MapWithRouting location={activeLocation} />}
    </section>
  );
}
